import { createApp, type App } from 'vue';
import './index.css';
import AppComponent from './index.vue';

let app: App | null = null;

const event_type = getButtonEvent('');
eventOn(event_type, () => {
  start();
});

function start() {
  if (app) {
    cleanup();
  }

  const $app = createScriptIdDiv();
  $('body').append($app);
  /**
   * jQuery  Shadow DOM 
   */
  const shadowRoot = $app.get(0)?.attachShadow({ mode: 'open' });
  const appRoot = document.createElement('div');
  shadowRoot?.appendChild(appRoot);

  if (shadowRoot) {
    copyStyleToShadowDOM(shadowRoot);
  }

  const onClose = () => {
    cleanup();
  };

  //  start()  Vue app 
  app = createApp(AppComponent);
  //  ShadowRoot  jQuery  Shadow DOM 
  if (shadowRoot) {
    app.provide('shadowRoot', shadowRoot);
  }
  app.provide('onClose', onClose);
  app.mount(appRoot);
  console.info('');
}

function cleanup() {
  if (app) {
    app.unmount();
    app = null;
  }
  destroyScriptIdDiv();
}

$(window).on('pagehide', () => {
  cleanup();
});

function copyStyleToShadowDOM(shadowRoot: ShadowRoot) {
  const headStyles = document.querySelectorAll('head > style');

  headStyles.forEach((styleNode: Element) => {
    const newStyleEl = parent.document.createElement('style');

    const $source = $(styleNode);
    const $dest = $(newStyleEl);

    $dest.text($source.text());

    $.each(styleNode.attributes, (_, attr) => {
      $dest.attr(attr.name, attr.value);
    });

    $(shadowRoot).prepend($dest);
  });
}

function createScriptIdDiv(): JQuery<HTMLDivElement> {
  return $('<div>').attr('script_id', getScriptId()) as JQuery<HTMLDivElement>;
}

function destroyScriptIdDiv(): void {
  $(`div[script_id="${getScriptId()}"]`).remove();
}
