ForLevel2Idx(idx)\"\n              class=\"flex flex-nowrap items-center gap-0.5\"\n            >\n              <span v-if=\"editMode\" class=\"drag-handle-l2 shrink-0 cursor-move\" title=\"\">  </span>\n              <div\n                role=\"tab\"\n                class=\"kk-text-base tab h-2.5 cursor-pointer px-0! whitespace-nowrap text-(--quick-send-text)\"\n                :class=\"{ 'tab-active': activeSecondIndex === idx }\"\n                @click=\"onClickLevel2(idx)\"\n              >\n                {{ entry.label }}\n              </div>\n            </div>\n            <span\n              v-if=\"editMode\"\n              class=\"ml-1 shrink-0 cursor-pointer rounded-sm border border-dashed border-(--quick-send-text) px-1 py-0\"\n              title=\"\"\n              @click.stop=\"openCreate('l2')\"\n            >\n              +\n            </span>\n          </div>\n        </div>\n      </div>\n\n      <!--  -->\n      <div class=\"breadcrumbs shrink-0 px-1 pt-0.5\">\n        <ul class=\"pl-0!\">\n          <li\n            v-for=\"(c, idx) in breadcrumbs\"\n            :key=\"`crumb-${activeTabIndex}-${activeSecondIndex}-${idx}-${gridPath.join('-')}`\"\n          >\n            <a v-if=\"idx < breadcrumbs.length - 1\" class=\"cursor-pointer\" @click=\"navigateTo(idx)\">{{ c }}</a>\n            <span v-else>{{ c }}</span>\n          </li>\n        </ul>\n      </div>\n\n      <!--  -->\n      <div class=\"mb-0 flex-1 overflow-y-auto px-1\">\n        <div\n          ref=\"gridContainer\"\n          class=\"gridContainer auto-grid grid gap-0.5\"\n          style=\"--auto-grid-min: calc(var(--spacing) * 9)\"\n        >\n          <div\n            v-for=\"(label, idx) in gridLabels\"\n            :key=\"makeIdForGridIdx(idx)\"\n            class=\"auto-grid-item cursor-pointer rounded bg-(--quick-send-bg-sec) px-0.5 py-0.5\"\n            :class=\"{ 'bg-(--quick-send-primary-color)/70': selectedGridIndex === idx }\"\n            :title=\"label\"\