le-[0.8]\n  sm:scale-[1]\n\"/>\n                <label for=\"replace-names-checkbox\" class=\"cursor-pointer\">user</label>\n              </div>\n              <div class=\"flex items-center gap-0.25\">\n                <input\nid=\"replace-char-checkbox\" v-model=\"replaceCharNames\" type=\"checkbox\" class=\"\n  scale-[0.8]\n  sm:scale-[1]\n\" />\n                <label for=\"replace-char-checkbox\" class=\"cursor-pointer\">char</label>\n              </div>\n            </div>\n          </div>\n          <div\n            class=\"cursor-pointer rounded border-none bg-(--quick-send-primary-color) px-1 py-0.5\"\n            :class=\"{ 'pointer-events-none': editMode }\"\n            @click=\"sendQuickText\"\n          >\n            \n          </div>\n        </div>\n      </div>\n    </div>\n  </div>\n  <EditKeyModal v-if=\"editOpen\" v-model=\"editOpen\" :snapshot=\"editSnapshot\" @save=\"handleEditSave\" />\n  <FixedPrefixSuffixModal\n    v-if=\"fixedPrefixSuffixModalOpen\"\n    v-model=\"fixedPrefixSuffixModalOpen\"\n    :data=\"fixedPrefixSuffixData\"\n    @save=\"handleFixedPrefixSuffixSave\"\n  />\n</template>\n\n<script lang=\"ts\" setup>\nimport { createReusableTemplate, useLocalStorage } from '@vueuse/core';\nimport gsap from 'gsap';\nimport { klona } from 'klona';\nimport { computed, inject, nextTick, onMounted, reactive, ref, toRaw, toRef, watch } from 'vue';\nimport EditKeyModal, { type EditSnapshot } from './EditKeyModal.vue';\nimport FixedPrefixSuffixModal, { type FixedPrefixSuffixData } from './FixedPrefixSuffixModal.vue';\nimport { type DeepKey, getByPath } from './utils/deep-utils';\nimport { isSingleKeyObject as isSingleKeyObjectUtil, updateContainerWithPayload } from './utils/quicksend';\nimport { useShadowDraggableList } from './utils/useShadowDraggable';\n\n/**\n * /\n */\nconst [DefinePrefixSuffixList, ReusePrefixSuffixList] = createReusableTemplate<{\n  list: any[];\n  selectedIndexes: Set<number>;\n  dragHandle: string;\n  containerClass?: string;\n  keyPrefix: string;\n  onItemClick: (item: any, idx: number) => void;\n  containerRef?: (el: any) => void;\n  onAddClick?: () => void;\n}>();\n\n/**\n * \n */\nconst editMode = ref(false);\n\n/**\n * \n */\nconst editOpen = ref(false);\n\n/**\n * \n */\nconst editSnapshot = ref<EditSnapshot | null>(null);\n\n/**\n * ID\n */\nconst editNodeId = ref<string | null>(null);\n\n/**\n * \n */\nconst rawData = reactive(getVariables({ type: 'script', script_id: getScriptId() }));\n\nif (!Array.isArray(rawData.data)) {\n  rawData.data = [];\n}\n\nif (typeof rawData.showTip !== 'boolean') {\n  rawData.showTip = true;\n}\n\n/**\n * \n *  toRef  useSortable \n */\nconst quickSendData = toRef(rawData, 'data');\n\n/**\n * \n */\nconst showAlertTip = ref(rawData.showTip);\n\n/**\n *  DOM \n */\nconst alertTip = ref<HTMLElement | null>(null);\n\n/**\n * \n */\nlet alertTipTimer: ReturnType<typeof setTimeout> | null = null;\n\n/**\n * \n */\nconst activeTabIndex = ref(0);\n\n/**\n * \n */\nconst activeSecondIndex = ref(0);\n\n/**\n * \"\"\n *\n * \n *\n * @param obj - \n * @returns  undefined\n *\n * @example\n * singleKeyOf({ \"\": \"\" })  * singleKeyOf({ \"a\": 1, \"b\": 2 })  * singleKeyOf(null)  */\nfunction singleKeyOf(obj: any): { key: string; value: any } | undefined {\n  if (!obj || typeof obj !== 'object') return undefined;\n  const keys = Object.keys(obj);\n  if (keys.length !== 1) return undefined;\n  const k = keys[0];\n  return { key: k, value: (obj as any)[k] };\n}\n\ntype DataPath = DeepKey[];\n\ninterface Level1Entry {\n  label: string;\n  value: any;\n  path: DataPath;\n  arrIndex: number;\n}\n\nconst level1Entries = computed<Level1Entry[]>(() =>\n  quickSendData.value.map((el: any, idx: number) => {\n    const sk = singleKeyOf(el);\n    if (sk) {\n      return {\n        label: sk.key,\n        value: sk.value,\n        path: [idx, sk.key] as DataPath,\n        arrIndex: idx,\n      };\n    }\n    return {\n      label: typeof el === 'string' ? el : String(idx),\n      value: el,\n      path: [idx] as DataPath,\n      arrIndex: idx,\n    };\n  }),\n);\n\nconst level1Labels = computed<string[]>(() => level1Entries.value.map(entry => entry.label));\n\n/**\n *  DOM \n */\nconst level1Container = ref<HTMLElement | null>(null);\nconst level2Container = ref<HTMLElement | null>(null);\nconst gridContainer = ref<HTMLElement | null>(null);\nconst prefixContainer = ref<HTMLElement | null>(null);\nconst suffixContainer = ref<HTMLElement | null>(null);\n\nconst showPrefixList = ref(false);\nconst showSuffixList = ref(false);\n\nconst prefixList = computed<any[]>(() => (rawData.perfix || rawData.prefix || []) as any[]);\nconst suffixList = computed<any[]>(() => (rawData.suffix || []) as any[]);\n\ntype Level2Entry = {\n  label: string;\n  value: any;\n  arrIndex: number;\n  key: string | null;\n  fromMultiKey: boolean;\n  path: DataPath;\n};\n\nconst level2Entries = computed<Level2Entry[]>(() => {\n  const level1Entry = level1Entries.value[activeTabIndex.value];\n  const arr = level1Entry?.value;\n  if (!Array.isArray(arr)) return [];\n  const basePath = level1Entry?.path ?? ([activeTabIndex.value] as DataPath);\n  const items: Level2Entry[] = [];\n  arr.forEach((el, arrIndex) => {\n    if (el == null) return;\n    const arrPath = [...basePath, arrIndex] as DataPath;\n    if (typeof el === 'object' && !Array.isArray(el)) {\n      const keys = Object.keys(el);\n      if (keys.length === 0) return;\n      if (keys.length === 1) {\n        const key = keys[0];\n        items.push({\n          label: key,\n          value: (el as any)[key],\n          arrIndex,\n          key,\n          fromMultiKey: false,\n          path: [...arrPath, key] as DataPath,\n        });\n        return;\n      }\n      keys.forEach(key => {\n        items.push({\n          label: key,\n          value: (el as any)[key],\n          arrIndex,\n          key,\n          fromMultiKey: true,\n          path: [...arrPath, key] as DataPath,\n        });\n      });\n      return;\n    }\n    items.push({\n      label: typeof el === 'string' ? el : String(arrIndex),\n      value: el,\n      arrIndex,\n      key: null,\n      fromMultiKey: false,\n      path: arrPath,\n    });\n  });\n  return items;\n});\n\n/**\n * \n */\nuseShadowDraggableList({\n  container: level2Container,\n  itemSelector: '.level2Container > div',\n  handleSelector: '.drag-handle-l2',\n  enabled: editMode,\n  onUpdate: (oldIndex, newIndex) => {\n    const level1Entry = level1Entries.value[activeTabIndex.value];\n    if (!level1Entry || !Array.isArray(level1Entry.value)) return;\n\n    const arr = [...level1Entry.value];\n    const [movedItem] = arr.splice(oldIndex, 1);\n    arr.splice(newIndex, 0, movedItem);\n\n    const path = level1Entry.path;\n    if (path.length === 1) {\n      rawData.data[path[0] as number] = arr;\n    } else if (path.length === 2) {\n      const arrIdx = path[0] as number;\n      const key = path[1] as string;\n      rawData.data[arrIdx] = { [key]: arr } as any;\n    }\n    replaceVariables(klona(rawData), { type: 'script', script_id: getScriptId() });\n  },\n});\n\nuseShadowDraggableList({\n  container: level1Container,\n  itemSelector: '.level1Container > div',\n  handleSelector: '.drag-handle-l1',\n  enabled: editMode,\n  onUpdate: (oldIndex, newIndex) => {\n    const arr = [...quickSendData.value];\n    const [movedItem] = arr.splice(oldIndex, 1);\n    arr.splice(newIndex, 0, movedItem);\n    rawData.data = arr as any;\n    replaceVariables(klona(rawData), { type: 'script', script_id: getScriptId() });\n  },\n});\n\nuseShadowDraggableList({\n  container: gridContainer,\n  itemSelector: '.gridContainer > div',\n  handleSelector: '.drag-handle-grid',\n  enabled: editMode,\n  onUpdate: (oldIndex, newIndex) => {\n    const baseEntry = gridBaseEntry.value;\n\n    if (!baseEntry) return;\n\n    //  baseEntry.path  gridPath \n    //  computeGridEntries  metas \n    let cur: any = toRaw(baseEntry.value ?? null);\n    let curr