  isLeaf: resolved.isLeaf,\n    value: resolved.isLeaf ? (resolved.value as string) : undefined,\n    children: makeChildrenSnapshot(resolved.value),\n  };\n  editOpen.value = true;\n}\n\n/**\n * \n *\n * @param scope - \n */\nfunction openCreate(scope: 'l1' | 'l2' | 'grid') {\n  editNodeId.value = `__create__:${scope}`;\n  editSnapshot.value = {\n    title: '',\n    name: '',\n    canRename: true,\n    isLeaf: false,\n    value: undefined,\n    children: [],\n    isCreate: true,\n    createLevel: scope,\n  };\n  editOpen.value = true;\n}\n\n/**\n * \n *\n * \n * \n * \n *\n * @param payload - \n */\nfunction handleEditSave(payload: {\n  newName?: string;\n  newValue?: string;\n  children: Array<{ id: string; newName?: string; newValue?: string }>;\n  __delete?: boolean;\n  __create?: boolean;\n  __createAsLeaf?: boolean;\n}) {\n  const id = editNodeId.value;\n  if (!id) return;\n\n  if (id.startsWith('__create__:')) {\n    const scope = id.slice(11);\n    const newName = payload.newName || '';\n\n    if (scope === 'prefix') {\n      const newItem = { [newName]: payload.newValue ?? '' };\n      if (!rawData.prefix && !rawData.perfix) {\n        rawData.prefix = [];\n      }\n      const list = rawData.perfix || rawData.prefix;\n      if (Array.isArray(list)) {\n        list.push(newItem);\n      }\n    } else if (scope === 'suffix') {\n      const newItem = { [newName]: payload.newValue ?? '' };\n      if (!rawData.suffix) {\n        rawData.suffix = [];\n      }\n      rawData.suffix.push(newItem);\n    } else if (scope === 'l1') {\n      const newItem = { [newName]: [] };\n      quickSendData.value.push(newItem);\n    } else if (scope === 'l2') {\n      const level1Entry = level1Entries.value[activeTabIndex.value];\n      if (level1Entry && Array.isArray(level1Entry.value)) {\n        const newItem = { [newName]: [] };\n        level1Entry.value.push(newItem);\n      }\n    } else if (scope === 'grid') {\n      const baseEntry = gridBaseEntry.value;\n      if (!baseEntry) return;\n\n      let targetArray: any = baseEntry.value;\n      for (const idx of gridPath.value) {\n        const step = computeGridEntries(targetArray, baseEntry.path);\n        targetArray = step.values[idx];\n      }\n\n      if (Array.isArray(targetArray)) {\n        const newItem = payload.__createAsLeaf ? { [newName]: payload.newValue ?? '' } : { [newName]: [] };\n        targetArray.push(newItem);\n      } else if (typeof targetArray === 'object' && targetArray !== null) {\n        if (payload.__createAsLeaf) {\n          (targetArray as any)[newName] = payload.newValue ?? '';\n        } else {\n          (targetArray as any)[newName] = [];\n        }\n      }\n    }\n\n    replaceVariables(rawData, { type: 'script', script_id: getScriptId() });\n    editOpen.value = false;\n    editNodeId.value = null;\n    editSnapshot.value = null;\n    return;\n  }\n\n  if (id.startsWith('prefix:')) {\n    const idx = Number(id.slice(7));\n    if (payload.__delete) {\n      const list = prefixList.value;\n      list.splice(idx, 1);\n      selectedPrefixIndexes.value = new Set(\n        _.chain([...selectedPrefixIndexes.value])\n          .filter(i => i !== idx)\n          .map(i => (i > idx ? i - 1 : i))\n          .value(),\n      );\n    } else {\n      const list = prefixList.value;\n      const item = list[idx];\n      if (item && typeof item === 'object') {\n        const oldKey = Object.keys(item)[0];\n        if (payload.newName && payload.newName !== oldKey) {\n          delete (item as any)[oldKey];\n          (item as any)[payload.newName] = payload.newValue ?? '';\n        } else if (payload.newValue != null) {\n          (item as any)[oldKey] = payload.newValue;\n        }\n      }\n    }\n    replaceVariables(rawData, { type: 'script', script_id: getScriptId() });\n    editOpen.value = false;\n    editNodeId.value = null;\n    editSnapshot.value = null;\n    return;\n  }\n\n  if (id.startsWith('suffix:')) {\n    const idx = Number(id.slice(7));\n    if (payload.__delete) {\n      const list = suffixList.value;\n      list.splice(idx, 1);\n      selectedSuffixIndexes.value = new Set(\n        _.chain([...selectedSuffixIndexes.value])\n          .filter(i => i !== idx)\n          .map(i => (i > idx ? i - 1 : i))\n          .value(),\n      );\n    } else {\n      const list = suffixList.value;\n      const item = list[idx];\n      if (item && typeof item === 'object') {\n        const oldKey = Object.keys(item)[0];\n        if (payload.newName && payload.newName !== oldKey) {\n          delete (item as any)[oldKey];\n          (item as any)[payload.newName] = payload.newValue ?? '';\n        } else if (payload.newValue != null) {\n          (item as any)[oldKey] = payload.newValue;\n        }\n      }\n    }\n    replaceVariables(rawData, { type: 'script', script_id: getScriptId() });\n    editOpen.value = false;\n    editNodeId.value = null;\n    editSnapshot.value = null;\n    return;\n  }\n\n  const resolved = resolveById(quickSendData.value, id);\n  if (!resolved) return;\n  const { container, keyInContainer } = resolved;\n\n  if (payload.__delete) {\n    if (Array.isArray(container)) {\n      container.splice(keyInContainer as number, 1);\n    } else if (typeof container === 'object') {\n      // \n      // \n      delete (container as any)[keyInContainer];\n\n      try {\n        const path = resolved.path;\n        const containerPath = path.slice(0, -1);\n        const parentPath = containerPath.slice(0, -1);\n        const idxInParent = containerPath[containerPath.length - 1];\n        const parentContainer =\n          parentPath.length === 0 ? quickSendData.value : getByPath(quickSendData.value, parentPath);\n\n        if (\n          parentContainer &&\n          Array.isArray(parentContainer) &&\n          typeof idxInParent === 'number' &&\n          container &&\n          typeof container === 'object' &&\n          Object.keys(container).length === 0\n        ) {\n          parentContainer.splice(idxInParent, 1);\n        }\n      } catch (_e) {\n        // \n      }\n    }\n  } else {\n    updateContainerWithPayload({ container, keyInContainer, payload });\n  }\n\n  replaceVariables(rawData, { type: 'script', script_id: getScriptId() });\n\n  editOpen.value = false;\n  editNodeId.value = null;\n  editSnapshot.value = null;\n}\n\nfunction togglePrefixList() {\n  showPrefixList.value = !showPrefixList.value;\n  if (showPrefixList.value) showSuffixList.value = false;\n}\n\nfunction toggleSuffixList() {\n  showSuffixList.value = !showSuffixList.value;\n  if (showSuffixList.value) showPrefixList.value = false;\n}\n\nfunction getPrefixSuffixKey(item: any): string {\n  if (item && typeof item === 'object' && !Array.isArray(item)) {\n    const keys = Object.keys(item);\n    if (keys.length > 0) return keys[0];\n  }\n  return '';\n}\n\nfunction getPrefixSuffixValue(item: any): string {\n  if (item && typeof item === 'object' && !Array.isArray(item)) {\n    const keys = Object.keys(item);\n    if (keys.length > 0) {\n      const value = item[keys[0]];\n      return typeof value === 'string' ? value : String(value);\n    }\n  }\n  return '';\n}\n\n/**\n * \n *\n * @param item - \n * @param idx - \n */\nfunction onPrefixItemClick(_item: any, idx: number) {\n  if (editMode.value) {\n    openEditPrefixSuffix(idx, 'prefix');\n    return;\n  }\n\n  if (selectedPrefixIndexes.value.has(idx)) {\n    selectedPrefixIndexes.value.delete(idx);\n  } else {\n    selectedPrefixIndexes.value.add(idx);\n  }\n\n  selectedPrefixIndexes.value = new Set(selectedPrefixIndexes.value);\n  //  watch applyMacroSubstitution\n}\n\n/**\n * \n *\n * @param item - \n * @param idx - \n */\nfunction onSuffixItemClick(_item: any, idx: number) {\n  if (editMode.value) {\n    openEditPrefixSuffix(idx, 'suffix');\n    return;\n  }\n\n  if (selectedSuffixIndexes.value.has(idx)) {\n    selectedSuffixIndexes.value.delete(idx);\n  } else {\n    selectedSuffixIndexes.value.add(idx);\n  }\n\n  selectedSuffixIndexes.value = new Set(selectedSuffixIndexes.value);\n  //  watch applyMacroSubstitution\n}\n\nfunction openEditPrefixSuffix(idx: number, type: 'prefix' | 'suffix') {\n  const list = type === 'prefix' ? prefixList.value : suffixList.value;\n  const item = list[idx];\n  if (!item) return;\n  const key = getPrefixSuffixKey(item);\n  const value = getPrefixSuffixValue(item);\n  editNodeId.value = `${type}:${idx}`;\n  editSnapshot.value = {\n    title: key,\n    name: key,\n    canRename: true,\n    isLeaf: true,\n    value: value,\n    children: [],\n  };\n  editOpen.value = true;\n}\n\n/**\n * /\n *\n * @param type - 'prefix'  'suffix'\n */\nfunction openCreatePrefixSuffix(type: 'prefix' | 'suffix') {\n  editNodeId.value = `__create__:${type}`;\n  editSnapshot.value = {\n    title: type === 'prefix' ? '' : '',\n    name: '',\n    canRename: true,\n    isLeaf: true,\n    value: '',\n    children: [],\n    isCreate: true,\n  };\n  editOpen.value = true;\n}\n\nfunction sendQuickText() {\n  const finalText = text.value;\n\n  triggerSlash(`/send ${finalText}|/trigger`);\n  if (onClose) onClose();\n}\n\n/**\n * \n */\nfunction appendSillyTavernText() {\n  const STText = $('#send_textarea').val();\n  if (STText === '' || STText === null) {\n    tavernText.value = '';\n    applyMacroSubstitution();\n    return;\n  }\n  tavernText.value = String(STText);\n  applyMacroSubstitution();\n}\n</script>\n\n<style lang=\"css\" scoped>\n.tab-active {\n  font-weight: 700;\n}\n\n.tab-active:before {\n  content: '';\n  display: block;\n  position: absolute;\n  border-top: none;\n  border-bottom: 2px solid var(--quick-send-primary-color);\n  width: 100%;\n  left: 0;\n  bottom: 0;\n}\n\n.auto-grid {\n  grid-template-columns: repeat(auto-fit, minmax(var(--auto-grid-min, 2.5rem), 1fr));\n}\n\n.auto-grid-item {\n  min-width: var(--auto-grid-min, 2.5rem);\n  display: flex;\n  align-items: center;\n  gap: 0.25rem;\n  width: 100%;\n}\n\n.auto-grid-item .grid-label {\n  flex: 1 1 auto;\n  min-width: 0;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n</style>\n"],"sourceRoot":""}]);
