/*
 *  Shadow DOM 
 * * 
 * -  document target  rootNodeShadowRoot  document
 * -  rootNode.elementFromPoint  document.elementFromPoint
 * -  ShadowRoot  document.body
 * * 
 * - "" DOM 
 * - ""
 * -  move/end 
 *
 * --- V2 (Refactored for Performance) ---
 * -  requestAnimationFrame (rAF)  onDragMove
 */
import { onBeforeUnmount, onMounted, ref, toValue, watch, type Ref } from 'vue';

type MaybeRef<T> = Ref<T> | T;

export type UseShadowDraggableListOptions = {
  // 
  container: Ref<HTMLElement | null>;
  // 
  itemSelector: string;
  // 
  handleSelector: string;
  // 
  enabled: MaybeRef<boolean>;
  // oldIndex/newIndex
  onUpdate: (oldIndex: number, newIndex: number) => void;
  // 
  draggingClass?: string;
  // 
  debug?: boolean;
};

type RootNode = Document | ShadowRoot;

export function useShadowDraggableList(opts: UseShadowDraggableListOptions) {
  const debug = opts.debug ?? false;
  const dlog = (...args: any[]) => {
    if (debug) console.log('[useShadowDraggable]', ...args);
  };
  const isDragging = ref(false);
  const startPos = { x: 0, y: 0 };
  const dragEl = ref<HTMLElement | null>(null);
  const ghostEl = ref<HTMLElement | null>(null);
  let originalDisplay: string | null = null;
  const dragItem = ref<HTMLElement | null>(null);
  const dragRoot = ref<RootNode | null>(null);
  const startIndex = ref<number | null>(null);
  const currentIndex = ref<number | null>(null);
  // 
  let lastPoint: { x: number; y: number } | null = null;

  // --- : rAF  ---
  /**  move  rAF  */
  let latestMoveEvent: Event | null = null;
  /** rAF  ID */
  let animationFrameId: number | null = null;

  // /
  function getPoint(ev: MouseEvent | TouchEvent): { clientX: number; clientY: number } | null {
    const touch = (ev as TouchEvent).touches?.[0];
    const p = touch ?? (ev as MouseEvent);
    if (p && typeof (p as any).clientX === 'number' && typeof (p as any).clientY === 'number') {
      return { clientX: (p as any).clientX, clientY: (p as any).clientY };
    }
    return null;
  }

  function findClosest(el: Element | null, selector: string): HTMLElement | null {
    if (!el) return null;
    const found = (el as any).closest?.(selector) as HTMLElement | null;
    return found ?? null;
  }

  // 
  function getValidItems(container: HTMLElement): HTMLElement[] {
    const allItems = Array.from(container.querySelectorAll(opts.itemSelector)) as HTMLElement[];
    return allItems.filter(it => {
      if (!container.contains(it)) return false;
      // 
      if (it.hasAttribute('data-draggable-placeholder')) return false;
      // 
      if (it.style.display === 'none') return false;
      // 
      const r = it.getBoundingClientRect();
      if (r.width === 0 || r.height === 0) return false;
      // 
      const hasHandle = it.querySelector(opts.handleSelector);
      if (!hasHandle) return false;
      return true;
    });
  }

  function getIndexInContainer(container: HTMLElement, item: HTMLElement): number {
    const items = getValidItems(container);
    return items.indexOf(item);
  }

  //  elementFromPoint  gap/
  // "/"
  function findOverItemByGeometry(
    container: HTMLElement,
    x: number,
    y: number,
  ): { item: HTMLElement | null; index: number } {
    const items = getValidItems(container);

    let best: { item: HTMLElement | null; index: number; score: number } = {
      item: null,
      index: -1,
      score: Number.NEGATIVE_INFINITY,
    };

    // 
    const scores: string[] | null = debug ? [] : null;
    for (let i = 0; i < items.length; i++) {
      const it = items[i];
      const r = it.getBoundingClientRect();
      // Y /
      const withinY = y >= r.top && y <= r.bottom;
      const yScore = withinY ? 1000 : -Math.min(Math.abs(y - r.top), Math.abs(y - r.bottom));
      // X 
      const cx = (r.left + r.right) / 2;
      const xScore = -Math.abs(x - cx);
      const score = yScore * 10 + xScore;
      if (scores) scores.push(`[${i}]:${score.toFixed(0)}(L${r.left.toFixed(0)}-R${r.right.toFixed(0)})`);
      if (score > best.score) best = { item: it, index: i, score };
    }
    return { item: best.item, index: best.index };
  }

  function createDrag(fromEl: HTMLElement, root: RootNode) {
    // 1. 
    const rect = fromEl.getBoundingClientRect();

    // 2. 
    const dragEl = fromEl.cloneNode(true) as HTMLElement;

    // 3.  "" CSS 
    dragEl.classList.add('shadow-draggable-drag');

    // 4. ===  (Style Baking) ===
    const computedStyles = window.getComputedStyle(fromEl);

    // 
    // CSS
    const propertiesToCopy = [
      'width',
      'height',
      'paddingTop',
      'paddingBottom',
      'paddingLeft',
      'paddingRight',
      'marginTop',
      'marginBottom',
      'marginLeft',
      'marginRight',
      'borderTopWidth',
      'borderBottomWidth',
      'borderLeftWidth',
      'borderRightWidth',
      'borderTopColor',
      'borderBottomColor',
      'borderLeftColor',
      'borderRightColor',
      'borderTopStyle',
      'borderBottomStyle',
      'borderLeftStyle',
      'borderRightStyle',
      'borderColor',
      'borderWidth',
      'borderStyle',
      'borderRadius',
      'fontFamily',
      'fontSize',
      'fontWeight',
      'lineHeight',
      'textAlign',
      'color',
      'backgroundColor',
      'boxShadow',
      'boxSizing',
    ];

    //  "16px"
    for (const prop of propertiesToCopy) {
      (dragEl.style as any)[prop] = computedStyles[prop as keyof CSSStyleDeclaration];
    }

    // 5. ===  () ===
    dragEl.style.position = 'fixed'; //  'fixed' 
    dragEl.style.left = `${rect.left}px`; // 
    dragEl.style.top = `${rect.top}px`; // 
    dragEl.style.zIndex = '999999'; // 
    dragEl.style.pointerEvents = 'none'; // 
    dragEl.style.margin = '0'; // 

    // 5.  DOM 
    const anyRoot = root as any;
    let mountTarget: Node | null = null;

    if (anyRoot && anyRoot.host) {
      mountTarget = root;
    } else if (document.body) {
      mountTarget = document.body;
    } else if ((root as Document).body) {
      mountTarget = (root as Document).body;
    }

    if (mountTarget) {
      mountTarget.appendChild(dragEl);
    } else {
      console.warn('[useShadowDraggable] Could not find mount target for drag element.');
    }

    return dragEl;
  }

  function createGhost(fromEl: HTMLElement) {
    // 1.  (Deep Clone)
    const g = fromEl.cloneNode(true) as HTMLElement;

    // 2. 
    const rect = fromEl.getBoundingClientRect();

    // 3. /
    g.classList.remove('shadow-draggable-drag'); // 
    g.classList.add('shadow-draggable-ghost'); // 
    g.setAttribute('data-draggable-placeholder', 'true'); // 

    // 4.  ()
    // CSS100%
    g.style.width = `${rect.width}px`;
    g.style.height = `${rect.height}px`;
    g.style.boxSizing = 'border-box'; //  padding  border 
    g.style.margin = '0'; // 
    // 
    g.style.position = '';
    g.style.top = '';
    g.style.left = '';
    g.style.opacity = '0.5'; // 

    return g;
  }

  function removeDrag() {
    if (dragEl.value && dragEl.value.parentNode) {
      dragEl.value.parentNode.removeChild(dragEl.value);
    }
    dragEl.value = null;
  }

  function onDragStart(ev: Event) {
    const container = opts.container.value;
    if (!toValue(opts.enabled) || !container) {
      return;
    }

    const target = (ev.target as Element) || null;
    if (!target) {
      return;
    }

    // 
    const handleEl = findClosest(target, opts.handleSelector);
    if (!handleEl || !container.contains(handleEl)) {
      return;
    }

    const itemEl = findClosest(handleEl, opts.itemSelector);
    if (!itemEl || !container.contains(itemEl)) {
      return;
    }

    // 
    const point = getPoint(ev as any);
    if (!point) {
      return;
    }

    ev.preventDefault();

    isDragging.value = true;
    dragItem.value = itemEl as HTMLElement;
    dragRoot.value = (target as any)?.getRootNode?.() as RootNode;

    startPos.x = point.clientX;
    startPos.y = point.clientY;
    lastPoint = { x: point.clientX, y: point.clientY };

    startIndex.value = getIndexInContainer(container, dragItem.value);
    currentIndex.value = startIndex.value;

    // 
    dragEl.value = createDrag(dragItem.value, dragRoot.value!);

    // ""
    ghostEl.value = createGhost(dragItem.value);
    if (ghostEl.value) {
      // 
      const nextSibling = dragItem.value.nextSibling;
      if (nextSibling) {
        container.insertBefore(ghostEl.value, nextSibling);
      } else {
        container.appendChild(ghostEl.value);
      }
    }

    //  Sortable.js  sortable-drag
    dragItem.value.classList.add('shadow-draggable-drag');
    originalDisplay = dragItem.value.style.display;
    dragItem.value.style.display = 'none';

    //  root /
    const moveEndBindings: Array<
      [
        type: keyof DocumentEventMap,
        handler: EventListenerOrEventListenerObject,
        options?: AddEventListenerOptions | boolean,
      ]
    > = [
      ['mousemove', onDragMove],
      ['touchmove', onDragMove, { passive: false }], //  passive: false  preventDefault
      ['mouseup', onDragEnd],
      ['mouseleave', onDragEnd],
      ['touchend', onDragEnd],
      ['touchcancel', onDragEnd],
    ];
    moveEndBindings.forEach(([type, handler, options]) =>
      dragRoot.value!.addEventListener(type, handler, options as any),
    );

    if (opts.draggingClass) container.classList.add(opts.draggingClass);
  }

  // --- : rAF  DOM  ---
  /**
   *  rAF 
   *  DOM /
   */
  function processDragMove() {
    // 1. 
    //  latestMoveEvent onDragMove 
    if (!latestMoveEvent || !isDragging.value || !dragItem.value || !dragRoot.value || !dragEl.value) {
      animationFrameId = null; //  ID
      return;
    }

    const container = opts.container.value;
    if (!container) {
      animationFrameId = null;
      return;
    }

    // 2. 
    const point = getPoint(latestMoveEvent as any);
    if (!point) {
      animationFrameId = null;
      return;
    }

    // 3. ( onDragMove )
    const dx = point.clientX - startPos.x;
    const dy = point.clientY - startPos.y;

    //  ()
    dragEl.value.style.transform = `translate3d(${dx}px, ${dy}px, 0)`;

    //  elementFromPoint ()
    const prevDisplay = dragEl.value.style.display;
    dragEl.value.style.display = 'none';
    const under = (dragRoot.value as any).elementFromPoint?.(point.clientX, point.clientY) as Element | null;
    dragEl.value.style.display = prevDisplay;

    //  ()
    let overItem = under ? findClosest(under, opts.itemSelector) : null;
    if (
      overItem &&
      (overItem as HTMLElement).hasAttribute &&
      (overItem as HTMLElement).hasAttribute('data-draggable-placeholder')
    ) {
      overItem = null;
    }
    // 
    const validItems = getValidItems(container);
    const isValidItem = overItem && container.contains(overItem) && validItems.includes(overItem as HTMLElement);
    let overIndex = isValidItem && overItem ? getIndexInContainer(container, overItem) : -1;

    if ((!overItem || overIndex < 0) && container) {
      const fallback = findOverItemByGeometry(container, point.clientX, point.clientY);
      overItem = fallback.item;
      overIndex = fallback.index;
    }

    if (overItem && overIndex >= 0) {
      // /()
      const r = overItem.getBoundingClientRect();
      const prev = lastPoint ?? { x: point.clientX - dx, y: point.clientY - dy };
      const mvx = point.clientX - prev.x;
      const mvy = point.clientY - prev.y;
      const primaryAxis: 'x' | 'y' = Math.abs(mvx) >= Math.abs(mvy) ? 'x' : 'y';
      const xEdge = Math.min(20, (r.right - r.left) * 0.1);
      const yEdge = Math.min(20, (r.bottom - r.top) * 0.1);

      let insertBefore: boolean;
      if (primaryAxis === 'x') {
        if (mvx >= 0) {
          insertBefore = point.clientX <= r.left + xEdge;
        } else {
          insertBefore = !(point.clientX >= r.right - xEdge);
        }
      } else if (mvy >= 0) {
        insertBefore = point.clientY <= r.top + yEdge;
      } else {
        insertBefore = !(point.clientY >= r.bottom - yEdge);
      }
      if (
        (primaryAxis === 'x' && point.clientX > r.left + xEdge && point.clientX < r.right - xEdge) ||
        (primaryAxis === 'y' && point.clientY > r.top + yEdge && point.clientY < r.bottom - yEdge)
      ) {
        const midInsert =
          primaryAxis === 'y' ? point.clientY < (r.top + r.bottom) / 2 : point.clientX < (r.left + r.right) / 2;
        // 
        insertBefore = insertBefore ?? (midInsert as any);
      }
      const targetIndex = insertBefore ? overIndex : overIndex + 1;

      if (targetIndex !== currentIndex.value) {
        currentIndex.value = targetIndex;

        if (ghostEl.value) {
          // DOM (DOM )
          if (ghostEl.value.parentNode) {
            ghostEl.value.parentNode.removeChild(ghostEl.value);
          }
          // 
          const items = getValidItems(container);
          if (targetIndex >= 0 && targetIndex < items.length) {
            //  targetIndex  (DOM )
            container.insertBefore(ghostEl.value, items[targetIndex]);
          } else if (items.length > 0) {
            // 
            const last = items[items.length - 1];
            container.insertBefore(ghostEl.value, last.nextSibling);
          } else {
            // 
            container.insertBefore(ghostEl.value, container.firstChild);
          }
        }
      }
    }

    // 
    lastPoint = { x: point.clientX, y: point.clientY };
    // 4. ()  rAF ID
    animationFrameId = null;
  }

  // --- : onDragMove () ---
  /**
   *  (mousemove / touchmove)
   *  rAF
   */
  function onDragMove(ev: Event) {
    if (!isDragging.value) return;

    // 
    ev.preventDefault();

    // 1. 
    latestMoveEvent = ev;

    // 2.  rAF
    //  processDragMove  60fps 
    if (animationFrameId === null) {
      animationFrameId = requestAnimationFrame(processDragMove);
    }
  }

  // --- : cleanup ---
  function cleanup() {
    // --- :  rAF ---
    if (animationFrameId !== null) {
      cancelAnimationFrame(animationFrameId);
      animationFrameId = null;
    }
    latestMoveEvent = null; // 
    // ---  ---

    const container = opts.container.value;
    if (opts.draggingClass && container) container.classList.remove(opts.draggingClass);

    if (dragRoot.value) {
      const moveEndBindings: Array<[keyof DocumentEventMap, EventListenerOrEventListenerObject]> = [
        ['mousemove', onDragMove],
        ['touchmove', onDragMove],
        ['mouseup', onDragEnd],
        ['mouseleave', onDragEnd],
        ['touchend', onDragEnd],
        ['touchcancel', onDragEnd],
      ];
      moveEndBindings.forEach(([type, handler]) => dragRoot.value!.removeEventListener(type, handler as any));
    }
    removeDrag();
    if (ghostEl.value && ghostEl.value.parentNode) {
      ghostEl.value.parentNode.removeChild(ghostEl.value);
    }
    ghostEl.value = null;
    if (dragItem.value) {
      //  Sortable.js 
      dragItem.value.classList.remove('shadow-draggable-drag');
      //  display
      dragItem.value.style.display = originalDisplay ?? '';

      originalDisplay = null;
    }
    dragItem.value = null;
    dragRoot.value = null;
    startIndex.value = null;
    currentIndex.value = null;
    isDragging.value = false;
    lastPoint = null;
  }

  // --- : onDragEnd ---
  function onDragEnd(_ev: Event) {
    if (!isDragging.value) return;

    // --- :  ---
    //  rAF 
    if (animationFrameId !== null) {
      cancelAnimationFrame(animationFrameId);
      animationFrameId = null;
    }
    //  rAF  move 
    //  currentIndex 
    if (latestMoveEvent) {
      processDragMove();
    }
    // ---  ---

    const oldIdx = startIndex.value;
    const newIdx = currentIndex.value;

    //  onUpdate
    //  DOM  UI 
    if (oldIdx != null && newIdx != null && oldIdx !== newIdx) {
      // 
      opts.onUpdate(oldIdx, newIdx);
    }

    //  Vue 
    //  setTimeout 0  Vue 

    setTimeout(() => {
      cleanup(); // cleanup  ( latestMoveEvent)
    }, 0);
  }

  function bindStartListeners() {
    const container = opts.container.value;
    if (!container) return;
    // 
    container.addEventListener('mousedown', onDragStart);
    container.addEventListener('touchstart', onDragStart, { passive: false }); //  passive: false  preventDefault
  }

  function unbindStartListeners() {
    const container = opts.container.value;
    if (!container) return;
    container.removeEventListener('mousedown', onDragStart);
    container.removeEventListener('touchstart', onDragStart);
  }

  onMounted(() => {
    if (toValue(opts.enabled)) bindStartListeners();
  });

  onBeforeUnmount(() => {
    unbindStartListeners();
    cleanup();
  });

  //  enabled 
  watch(
    () => toValue(opts.enabled),
    val => {
      if (val) bindStartListeners();
      else {
        unbindStartListeners();
        cleanup(); // 
      }
    },
  );

  //  v-if/v-show 
  watch(
    () => opts.container.value,
    (el, old) => {
      // 
      if (old) {
        old.removeEventListener('mousedown', onDragStart);
        old.removeEventListener('touchstart', onDragStart);
      }
      if (el && toValue(opts.enabled)) {
        bindStartListeners();
      }
    },
  );

  return {
    isDragging,
  };
}
